export const composeAsyncValidators = validators => {
  return async (value, formValues) => {
    for (let i = 0; i < validators.length; ++i) {
      const validator = validators[i];
      try {
        const result = await validator(value, formValues);
        if (result) {
          return result;
        }
      } catch (e) {
        console.error(e);
        return `Failed to run validation: ${e.toString()} `;
      }
    }
  };
};