import { create } from 'zustand';
export const useSystemSettingsStore = create()((set, get) => ({
  systemSettings: undefined,
  getSystemSettings: () => {
    const systemSettings = get().systemSettings;
    if (systemSettings === undefined) {
      throw new Error('SystemSettings not loaded');
    }
    return systemSettings;
  },
  setSystemSettings: systemSettings => set({
    systemSettings
  }),
  getSystemSetting: settingsKey => get().getSystemSettings()[settingsKey]
}));
export const useSetSystemSettings = () => useSystemSettingsStore(state => state.setSystemSettings);
export const useSystemSettings = () => useSystemSettingsStore(state => state.getSystemSettings());
export const useSystemSetting = settingsKey => useSystemSettingsStore(state => state.getSystemSetting(settingsKey));