import { useCallback } from 'react';
import { useQueryParam, StringParam } from 'use-query-params';
import { z } from 'zod';
import { getSchemaPropertyForPath } from '../models/path';
import { useSchemaFromHandle } from '../schemas';
const SortQueryParam = StringParam;
const sortOrderDirections = ['asc', 'desc'];
const sortOrderStringSchema = z.string().refine(value => value.split(':').length === 2).transform(value => value.split(':')).pipe(z.tuple([z.string(),
// columnPath
z.enum(sortOrderDirections) // order
]));
const parseSortOrderString = value => {
  const parsed = sortOrderStringSchema.safeParse(value);
  if (parsed.success) {
    return parsed.data;
  }
  return undefined;
};
const formatSortOrderToString = value => `${value[0]}:${value[1]}`;
export const isValidSortPathForSchema = (schema, path) => {
  const schemaProperty = getSchemaPropertyForPath(schema, path);

  // sorting for metadata-API only works on simple and persisted properties
  if (schemaProperty && schemaProperty.simple) {
    return true;
  }
  return false;
};
export const useSectionListSortOrder = () => {
  const schema = useSchemaFromHandle();
  const [sortOrderParam, setSortOrderParam] = useQueryParam('sort', SortQueryParam, {
    removeDefaultsFromUrl: true
  });
  const setSortOrder = useCallback(value => {
    setSortOrderParam(value ? formatSortOrderToString(value) : undefined);
  }, [setSortOrderParam]);
  let parsedSortOrder = parseSortOrderString(sortOrderParam);
  if (parsedSortOrder) {
    const [path] = parsedSortOrder;
    if (!isValidSortPathForSchema(schema, path)) {
      parsedSortOrder = undefined;
    }
  }
  return [parsedSortOrder, setSortOrder];
};
export const useSortOrderQueryParams = () => {
  const [sortOrder] = useSectionListSortOrder();
  return sortOrder ? formatSortOrderToString(sortOrder) : undefined;
};