// export interface useSelecto
import { memoize } from 'lodash';
import { useState, useMemo, useCallback } from 'react';
import { canEditModel } from '../../lib';
export function useSelectedModels(options) {
  const [selectedModels, setSelectedModels] = useState(() => new Set((options === null || options === void 0 ? void 0 : options.intialSelected) || []));
  const add = useCallback(ids => {
    const addIds = Array.isArray(ids) ? ids : [ids];
    setSelectedModels(prevSelected => {
      const newSelected = new Set(prevSelected);
      addIds.forEach(id => newSelected.add(id));
      return newSelected;
    });
  }, [setSelectedModels]);
  const remove = useCallback(ids => {
    const removeIds = Array.isArray(ids) ? ids : [ids];
    setSelectedModels(prevSelected => {
      const newSelected = new Set(prevSelected);
      removeIds.forEach(id => newSelected.delete(id));
      return newSelected;
    });
  }, [setSelectedModels]);
  const toggle = useCallback((ids, checked) => {
    if (checked) {
      add(ids);
    } else {
      remove(ids);
    }
  }, [add, remove]);
  const clearAll = useCallback(() => setSelectedModels(new Set()), [setSelectedModels]);
  const checkAllSelected = useMemo(() => memoize(data => {
    if (!data) {
      return false;
    }
    return data.filter(m => canEditModel(m)).every(model => selectedModels.has(model.id));
  }), [selectedModels]);
  return {
    selectedModels,
    checkAllSelected,
    add,
    remove,
    toggle,
    clearAll
  };
}