export const useSidebarLinks = () => {
  const isSectionAuthorized = useIsSectionAuthorizedPredicate();
  return useMemo(() => {
    const authorizedSidebarLinks = Object.values(sidebarLinks).map(_ref => {
      let {
        label,
        links
      } = _ref;
      return {
        label,
        links: links.filter(_ref2 => {
          let {
            section
          } = _ref2;
          return isSectionAuthorized(section);
        })
      };
    }).filter(_ref3 => {
      let {
        links
      } = _ref3;
      return links.length > 0;
    });
    return authorizedSidebarLinks;
  }, [isSectionAuthorized]);
};