export const hasWriteAccess = access => !!access.write;
export const hasDeleteAccess = access => !!access.delete;
export const canEditModel = model => hasWriteAccess(model.access);
export const canDeleteModel = model => hasDeleteAccess(model.access);
const parseAccessPart = accessPart => {
  const canRead = accessPart[0] === 'r';
  return {
    read: canRead,
    write: canRead && accessPart[1] === 'w'
  };
};

// metadata is first part, data second, rest (other) parts are not used
// eg. rw------ = metadata: rw, data: --
const publicAccessRegex = /^(r-|rw|--)(r-|rw|--)(-){4}$/;
export const parseAccessString = accessString => {
  const matches = accessString.match(publicAccessRegex);
  if (!matches) {
    return null;
  }
  const [, metadata, data] = matches;
  return {
    metadata: parseAccessPart(metadata),
    data: parseAccessPart(data)
  };
};
const accessPartToString = accessPart => {
  if (accessPart.write) {
    return 'rw';
  }
  return accessPart.read ? 'r-' : '--';
};
export const formatAccessToString = publicAccess => {
  const metadata = accessPartToString(publicAccess.metadata);
  const data = accessPartToString(publicAccess.data);
  return metadata + data + '----';
};