import { useFormState } from 'react-final-form';
const formStateSubscriptions = {
  errors: true,
  submitError: true,
  submitFailed: true,
  hasValidationErrors: true,
  hasSubmitErrors: true,
  dirtySinceLastSubmit: true,
  modifiedSinceLastSubmit: true
};
export const useFormStateErrors = () => {
  const {
    dirtySinceLastSubmit,
    errors,
    hasSubmitErrors,
    hasValidationErrors,
    submitError,
    submitFailed,
    modifiedSinceLastSubmit
  } = useFormState({
    subscription: formStateSubscriptions
  });
  const hasAnyError = !!(hasSubmitErrors || hasValidationErrors);

  // should only show errors after trying to submit
  const shouldShowErrors = hasAnyError && submitFailed && !dirtySinceLastSubmit || submitFailed && hasSubmitErrors;
  return {
    dirtySinceLastSubmit,
    hasSubmitErrors,
    hasValidationErrors,
    shouldShowErrors,
    submitError,
    submitFailed,
    validationErrors: errors,
    modifiedSinceLastSubmit
  };
};