import { useCallback, useMemo } from 'react';
import { useQueryParam, NumericObjectParam, withDefault } from 'use-query-params';
const defaultPaginationQueryParams = {
  page: 1,
  pageSize: 20
};
export const PAGE_SIZES = [5, 10, 20, 30, 40, 50, 75, 100];
const paginationQueryParamsConfig = withDefault(NumericObjectParam, defaultPaginationQueryParams);
const validatePagerParams = params => {
  if (!params) {
    return defaultPaginationQueryParams;
  }
  const isValid = Object.values(params).every(value => value && !isNaN(value));
  if (!isValid) {
    return defaultPaginationQueryParams;
  }
  const pageSize = params.pageSize;
  const page = params.page;

  // since pageSize can be changed in URL, find the closest valid pageSize
  const validatedPageSize = PAGE_SIZES.reduce((prev, curr) => Math.abs(curr - pageSize) < Math.abs(prev - pageSize) ? curr : prev);
  return {
    page,
    pageSize: validatedPageSize
  };
};
export const usePaginationQueryParams = () => {
  const [params, setParams] = useQueryParam('pager', paginationQueryParamsConfig, {
    removeDefaultsFromUrl: true
  });
  return useMemo(() => [validatePagerParams(params), setParams], [params, setParams]);
};
export function useUpdatePaginationParams(pager) {
  const [, setParams] = usePaginationQueryParams();
  const getPrevPage = useCallback(() => {
    if (!(pager !== null && pager !== void 0 && pager.prevPage)) {
      return false;
    }
    setParams(prevPager => ({
      ...prevPager,
      page: pager.page - 1
    }));
    return true;
  }, [pager, setParams]);
  const goToPage = useCallback(page => {
    if (!(pager !== null && pager !== void 0 && pager.pageCount) || page > pager.pageCount) {
      return false;
    }
    setParams(prevPager => ({
      ...prevPager,
      page
    }));
    return true;
  }, [pager, setParams]);
  const changePageSize = useCallback(pageSize => {
    setParams(prevPager => ({
      ...prevPager,
      pageSize: pageSize
    }));
    return true;
  }, [setParams]);
  return {
    getPrevPage,
    goToPage,
    changePageSize,
    pager
  };
}