import { getTranslatedProperty } from '../../constants/translatedModelProperties';
import { uniqueBy } from '../../utils';
import { defaultModelViewConfig, modelListViewsConfig } from './sectionListViewsConfig';
export const toModelPropertyDescriptor = propertyConfig => {
  if (typeof propertyConfig === 'string') {
    return {
      label: getTranslatedProperty(propertyConfig),
      path: propertyConfig
    };
  }
  return propertyConfig;
};
export const toFilterDescriptor = propertyConfig => {
  if (typeof propertyConfig === 'string') {
    return {
      label: getTranslatedProperty(propertyConfig),
      filterKey: propertyConfig
    };
  }
  return propertyConfig;
};
const resolveFilterConfig = part => {
  const {
    default: defaultFilters,
    available: defaultAvailableFilers
  } = defaultModelViewConfig.filters;
  const mergedAvailableDescriptors = uniqueBy([part.available || [], part.overrideDefaultAvailable ? [] : defaultAvailableFilers || [], part.default || []].flat().map(propConfig => toFilterDescriptor(propConfig)), prop => prop.filterKey);
  const defaultPropConfig = part.default || defaultFilters || [];
  const defaultDescriptors = defaultPropConfig.map(propConfig => toFilterDescriptor(propConfig));
  return {
    available: mergedAvailableDescriptors,
    default: defaultDescriptors
  };
};
const resolveColumnConfig = part => {
  const {
    default: defaultFilters,
    available: defaultAvailableFilers
  } = defaultModelViewConfig.columns;
  const mergedAvailableDescriptors = uniqueBy([part.available || [], part.overrideDefaultAvailable ? [] : defaultAvailableFilers || [], part.default || []].flat().map(propConfig => toModelPropertyDescriptor(propConfig)), prop => prop.path);
  const defaultPropConfig = part.default || defaultFilters || [];
  const defaultDescriptors = defaultPropConfig.map(propConfig => toModelPropertyDescriptor(propConfig));
  return {
    available: mergedAvailableDescriptors,
    default: defaultDescriptors
  };
};

// merge the default modelViewConfig with the modelViewsConfig for each section
const resolveListViewsConfig = () => {
  const merged = {};
  Object.entries(modelListViewsConfig).forEach(viewConfig => {
    const [sectionName, sectionViewConfig] = viewConfig;
    merged[sectionName] = {
      columns: resolveColumnConfig(sectionViewConfig.columns),
      filters: resolveFilterConfig(sectionViewConfig.filters)
    };
  });
  return merged;
};
const resolvedModelViewsConfig = resolveListViewsConfig();
const resolvedDefaultConfig = {
  columns: resolveColumnConfig(defaultModelViewConfig.columns),
  filters: resolveFilterConfig(defaultModelViewConfig.filters)
};
export const getViewConfigForSection = sectionName => {
  const resolvedConfig = resolvedModelViewsConfig[sectionName];
  if (resolvedConfig) {
    return resolvedConfig;
  }
  return resolvedDefaultConfig;
};
export const getColumnsForSection = sectionName => {
  const view = getViewConfigForSection(sectionName);
  return view.columns;
};
export const getFiltersForSection = sectionName => {
  const view = getViewConfigForSection(sectionName);
  return view.filters;
};